import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
      curentRouter: "img"
    },
    mutations: {
      setRouter (state, payload) {
        state.curentRouter = payload;
      }
    }
})

export default store;


